.carousel-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.carousel-wrapper {
  height: 100%;
  display: flex;
  width: 100%;
  position: relative;
}

.carousel-content-wrapper {
  width: 100%;
  overflow-y: visible;
  overflow-x: hidden;
  height: 100%;
}

.carousel-content {
  margin-top: 2vw;
  margin-left: 40px;
  height: 100%;
  display: flex;
  transition: all 250ms linear;
  -ms-overflow-style: none; /* hide scrollbar in IE and Edge */
  scrollbar-width: none; /* hide scrollbar in Firefox */
}

/* hide scrollbar in webkit browser */
.carousel-content::-webkit-scrollbar,
.carousel-content::-webkit-scrollbar {
  display: none;
}

.carousel-content > * {
  /* height: 100%;
  width: 100%; */
  flex-shrink: 0;
  flex-grow: 1;
}

.carousel-content.show-2 > * {
  width: 50%;
}

.carousel-content.show-3 > * {
  width: calc(100% / 3);
}

.carousel-content.show-4 > * {
  width: calc(100% / 4);
}
.carousel-content.show-6 > * {
  width: calc(100% / 6);
}
.left-arrow,
.right-arrow {
  position: absolute;
  z-index: 1;
  top: 50%;
  transform: translateY(-50%);
  width: 48px;
  height: 48px;
  border-radius: 24px;
  background-color: white;
  border: 1px solid #ddd;
}

.left-arrow {
  left: 10px;
}

.right-arrow {
  right: 10px;
}

@media (hover: none) and (pointer: coarse) {
  .left-arrow,
  .right-arrow {
    display: none;
  }
}
